#root,
.vkuiPanel__in,
.Panel__in {
  background: no-repeat url("../img/Background.svg");
  background-size: cover;
  background-position: center;
}

.YourBalance {
  user-select: none;
  font-family: ui-rounded, "SF Pro Rounded", system-ui, san-serif;
  -webkit-text-stroke: 2px #7e3e1e;
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  color: #f8d94f;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 48px;
  width: 100%;
}
.Counter {
  user-select: none;
  font-family: ui-rounded, "SF Pro Rounded", system-ui, san-serif;
